/********** Main **********/
.App {
  text-align: center;
  font-size: x-large;
  min-height: 100vh;
}

.App-mobile {
  text-align: center;
  font-size: medium;
  min-height: 100vh;
}

/********** Content **********/
.container {
  margin: auto;
  /* font-size:x-large; */
  display: flex;
  justify-content: center;
  padding-bottom: 4rem;
  min-height: 100%;
  position: relative;
}

.content {
  width: 70vw;
}

#firstSection {
  display: flex;
  align-items: center;
}

#picSection {
  display: flex;
  justify-content: center;
}

/********** Header **********/
.App-header {
  border-bottom: 3px solid #A89870;
  min-height: 120px;
  display: flex;
  align-items: flex-start; 
  padding-top: 20px;
  justify-content: center;
  color: black;
}

.App-logo {
  height: 100px;
  pointer-events: none;
}

.header-content {
  width: 70vw;
  display: flex;
  justify-content: space-between;
  /* align-items: flex-end; */
  align-items: center;
}

.header-content ul {
  list-style: none;
  text-align: right;
}

.App-header-mobile {
  border-bottom: 3px solid #A89870;
  min-height: 80px;
  display: flex;
  align-items: flex-start; 
  justify-content: center;
  color: black;
  font-size: medium;
  padding-top: 10px;
}

.App-logo-mobile {
  height: 60px;
  pointer-events: none;
}

/********** Footer **********/
.App-footer {
  /* position: fixed; */
  position: relative;
  bottom: 0;
  height: 4rem;
  border-top: 3px solid #A89870;
  color: black;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: white;
  margin-top: 10px;
}

.footer-content {
  width: 70vw; 
  padding: 3px;
  font-size: large;
}

.footer-content-mobile {
  width: 70vw; 
  padding: 3px;
  font-size: medium;
  height: 4rem;
}


.alignLeft {
  height: 4rem;
	float: left;
  margin: 0px;
  display: flex;
  align-items: center;
}

.alignRight {
  color: #A89870;
  height: 4rem;
	float: right;
  margin: 0px;
  display: flex;
  align-items: center;
}

.fc {
  padding-left: 50px;
  width: 300px;
  text-align: left;
}

.link, .link:visited {
  text-decoration: none;
  color: black;
}

.link:hover {
  color: #A89870;
}

/********** Sidebar **********/
.sidebar {
  position: fixed;
  right: 0px;
  min-width: 100px;
  align-items: right; 
  width: 5vw;
}

.sidebar ul {
  list-style: none;
  margin: 100px 0 0 0;
  justify-content: space-between;
}

.sidebar ul li {
  padding: 30px 0;
}

/********** Popup **********/
.popup-content {
  margin: auto;
  background: #A89870;
  width: 50%;
  padding: 5px;
}
.popup-arrow {
  color: #A89870;
  top: calc(50% - 3px);
  right: -8px;
}
[role='tooltip'].popup-content {
  width: 200px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 3px;
  border-radius: 10px;
  color: white;
}

.popup-overlay {
  background: rgba(0, 0, 0, 0.5);
}
[data-popup='tooltip'].popup-overlay {
  background: transparent;
}

/********** Impressum & Datenschutz **********/
.impressum {
  margin: auto;
  font-size: large;
  display: flex;
  justify-content: center;
  padding-bottom: 4rem;
}

/********** Scroll to top **********/
.top-to-btm{
  position: relative;
}
.icon-position{
  position: fixed;
  bottom: 4.7rem;
  right: 5px;
  z-index: 20;
}
.icon-style{
  background-color: #A89870;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  color: #fff;
  cursor: pointer;
  animation: movebtn 3s ease-in-out infinite;
  transition: all .5s ease-in-out;
}
.icon-style:hover{
  animation: none;
  background: black;
  color: white;
}
